<script>
import Constant from '@/Constant'
import Pagination from './Pagination.vue'
import FixOption from '@/components/FixOption.vue'

export default {
  components: { 
        Pagination,
        FixOption,
    },
    name: 'GridView',
    data: function(){
        return {
            show: true,
            colorClass: [
                {'r' : '#1234'},
                {'g' : '#1234'},
                {'b' : '#1234'},
            ]
        }
    },
    props: {
        items : Array,
        catelist: Array,
        post: {String}
    },
    computed: {
        itemprice(){
            return this.items.map(item => `${item}`.replace(/\B(?=(\d{3})+(?!\d))/g, ','))
        },

    },
    methods:{
        eventOpenModal:function(e){
            this.$store.dispatch(Constant.MODAL_OPEN,{modalView:'ModalOption',id:e})
        },
        listShow:function(e){
            if(e == !true){
                this.show = false
            } else if (e == true){
                this.show = true
            }
        },
    }
}
</script>
